var toDetail = function(param) {
    if(this.$route.params.w==param){
        this.$Notice.error({
            title: '',
            duration:0.75
        });
    }else{
        //   
        //watchroute detail
        // watch: {
        //     $route(to, from) {
        //       // ...
        //       //$router.go(0),ios
        //       //window.location.reload()
        //     }
        // }
        
        this.$router.push({name:'Detail', params: { w:param }})
        this.$Notice.success({
            title: '',
            duration:0.75
        });
    }
}
    
export default toDetail;


