<template>
<div>
    <pageHeader :menuName="menu"></pageHeader>
     <Table border :columns="columns" :data="pages.pageData">
        <template slot-scope="{ row }" slot="name">
            <strong>{{ row.name }}</strong>
        </template>
        <template slot-scope="{ index }" slot="action">
            
            <Button type="error" size="small" @click="remove(index)">Delete</Button>
        </template>
    </Table>
    <Page :total="pages.pageTotal" :page-size="pages.pageSize" @on-change="pageChange" :current="pages.pageNum"/>
</div>
   
</template>

<script>
import PageHeader from './pageHeader'
export default {
    
    name:'users',
    props: ['datas'],
     data () {
            return {
                //
                columns: [
                    {
                        title: '',
                        slot: 'name'
                    },
                    {
                        title: '',
                        key: 'phoneNumber'
                    },
                    {
                        title: '',
                        key: 'id'
                    },
                    {
                        title: '',
                        slot: 'action',
                        width: 150,
                        align: 'center'
                    }
                ],
                //
                pages:{
                    pageNum:1,
                    pageTotal:10,
                    pageSize:5,
                    pageData:[]
                },
                menu:'users'
            }
        },
        components:{
        PageHeader
    },
        methods: {
            show (index) {
                this.$Modal.info({
                    title: 'User Info',
                    content: `Name${this.pageData[index].name}<br>PhoneNumber${this.pageData[index].phoneNumber}<br>Id${this.pageData[index].id}`
                })
            },
            remove (index) {
                this.pages.pageData.splice(index, 1);
            },
            //
            pageInit(){
                this.pages.pageNum = 1;
                this.pages.pageTotal = this.datas.length*5,
                // Array.from()  
                this.pages.pageData = Array.from(this.datas[0].datas)
             
            },
            //
            pageChange(i){
                this.pages.pageNum = i;
                this.pages.pageSize = this.datas[i-1].datas.length
                this.pages.pageData = Array.from(this.datas[i-1].datas)
            
            }
        },
        created:function(){
            
           
        },
        mounted:function () {  
            // console.log("mounted");
            this.pageInit()
        }
}
</script>

<style>

</style>


