import axios from 'axios'


const timeout = 3000
//
const axiosConfig = {
    timeout,
    withCredentials: true,
    headers:{
        'X-Requested-With': 'XMLHttpRequest'
    }
}

// 
axios.interceptors.request.use(function (config) {
    // 
    return config;
  }, function (error) {
    // 
    return Promise.reject(error);
  });

// 
axios.interceptors.response.use(function (response) {
    // 
    return response;
  }, function (error) {
    // 
    return Promise.reject(error);
  });

  
//
export default (method, url, option = {}) => {
    // 
    method = (method || '').toUpperCase()
    //
    const config = option.config || {}
    //url
    const urlConfig = {method,url}
    //postData  urlConfig.data   axiosdata  post put paramsget method
    if(method =='POST'){
      urlConfig.data = option.data
      // console.log(option);
    }else if(method == 'GET'){
      urlConfig.params = option.data
    }
    
    // console.log({
    //   ...axiosConfig,//
    //   ...urlConfig,//url
    //   ...config//
    // })
    //axios API
    return axios({
        ...axiosConfig,//
        ...urlConfig,//url
        ...config//
    }).then(ret => ret.data)
}





