<template>
    <div>
        <pageHeader :menuName="menu"></pageHeader>
        <Tabs type="card">
            <TabPane label="">
                <Form
                    ref="formValidate"
                    :model="formValidate"
                    :rules="ruleValidate"
                    :label-width="80"
                    :class="datePiker"
                >
                    <FormItem label="" prop="name">
                        <Input
                            v-model="formValidate.name"
                            placeholder=""
                        ></Input>
                    </FormItem>
                    <FormItem label="" prop="phonenumber">
                        <Input
                            v-model="formValidate.phonenumber"
                            placeholder=""
                        ></Input>
                    </FormItem>
                    <FormItem label="" prop="trueName">
                        <Input
                            v-model="formValidate.trueName"
                            placeholder=""
                        ></Input>
                    </FormItem>
                    <FormItem label="" prop="id">
                        <Input
                            v-model="formValidate.id"
                            placeholder=""
                        ></Input>
                    </FormItem>
                    <FormItem label="" prop="birthday">
                        <DatePicker
                            type="date"
                            placeholder=""
                            v-model="formValidate.birthday"
                            @on-open-change="dateChange"
                        ></DatePicker>
                    </FormItem>
                    <FormItem label="" prop="gender">
                        <RadioGroup v-model="formValidate.gender">
                            <Radio label="male"></Radio>
                            <Radio label="female"></Radio>
                        </RadioGroup>
                    </FormItem>
                    <FormItem>
                        <Button
                            type="primary"
                            @click="handleSubmit('formValidate')"
                            ></Button
                        >
                    </FormItem>
                </Form>
            </TabPane>
            <TabPane label=""></TabPane>
            <TabPane label=""></TabPane>
        </Tabs>
    </div>
</template>

<script>
import PageHeader from "./pageHeader";
export default {
    name: "AccountInfo",
    props: ["datas"],
    data() {
        return {
            //
            formValidate: {
                name: this.datas.name,
                phonenumber: this.datas.phonenumber.toString(),//String 
                trueName: this.datas.trueName,
                id: this.datas.id.toString(),
                birthday: this.datas.birthday,
                gender: this.datas.gender,
            },
            //
            ruleValidate: {
                name: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                ],
                phonenumber: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                    { len:11, message: "", trigger: "change" },
                ],
                trueName: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                ],
                id: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                
                    },
                    { len: 18, message: "", trigger: 'change' },
                ],
                birthday: [
                    {
                        required: true, type: 'date', message: 'Please select the date', trigger: 'change' 
                    },
                ],
                gender: [
                    {
                        required: true,
                        message: "",
                        trigger: "change",
                    },
                ],
            },
            menu: "accountManage",
            datePiker:'account-info',
            
        };
    },
    components: {
        PageHeader,
    },
    methods: {
        //
        handleSubmit: function (name) {
            //validate  Boolean  Promise
            // callback

            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$Message.success("!");
                } else {
                    this.$Message.error("!");
                }
            });
        },
        dateChange:function (bool) {  
            
            if(bool){
                this.datePiker = "account-info data-pike"
            }else{
                this.datePiker = "account-info"
            }
        }
        //test
        // test:function () {
        //     console.log('accountInfo >');
        //     console.log(this.datas);

        // }
    },
    mounted() {
        // this.test();
    },
};
</script>

<style scoped>
.account-info {
    width: 500px;
    transition: 0.75s;
}
.data-pike{
    margin-bottom: 150px;
    transition: 0.5s;
}
</style>


