<template>
  <div>
      <pageHeader :menuName="menu"></pageHeader>
      <Table border :columns="columns" :data="this.pages.pageData">
        <!--  columns  slot  Table  slot  slot-scope
        slot-scope  3  row column index -->
        <template slot-scope="{ row }" slot="id">
            <strong>{{ row.id }}</strong>
        </template>
        <template slot-scope="{ index }" slot="action">
            
            <Button type="error" size="small" @click="remove(index)">Delete</Button>
        </template>
    </Table>
    <Page :total="pages.pageTotal" :page-size="pages.pageSize" @on-change="pageChange" :current="pages.pageNum"/>
  </div>
</template>

<script>
import PageHeader from './pageHeader'
export default {
    name:'Order',
    props: ['datas'],
    data:function(){
        return{
            menu:'orderManager',
            columns: [
                    {
                        title: '',
                        slot: 'id',
                        width:150
                    },
                    {
                        title: '',
                        key: 'date',
                        width:120
                    },
                    {
                        title: '',
                        key: 'name',
                        width:170
                    },
                    {
                        title: '',
                        key: 'count'
                    },
                    {
                        title: '',
                        key: 'unitPrice'
                    },
                    {
                        title: '',
                        key: 'totalPrice',
                        width:'100px'
                    },
                    {
                        title: '',
                        key: 'state'
                    },
                    {
                        title: '',
                        slot: 'action',
                        width: 120,
                        align: 'center'
                    }
                ],
            //
            pages:{
                    pageNum:1,
                    pageTotal:10,
                    pageSize:5,
                    pageData:[]
                },
                initialized:false
        }
    },
    components:{
        PageHeader
    },
    methods:{
        show (index) {
                this.$Modal.info({
                    title: 'User Info',
                    content: `Name${this.pageData[index].name}<br>PhoneNumber${this.pageData[index].phoneNumber}<br>Id${this.pageData[index].id}`
                })
            },
            remove (index) {
                this.pages.pageData.splice(index, 1);
            },
            // //
            // pageInit(){
            //     // 
            //     var _pages = this.pages;
            //     var _datas = this.datas;
            //     _pages.pageNum = 1;
            //     _pages.pageTotal = _datas.length*5,
            //     // Array.from()  
            //     _pages.pageData = Array.from(_datas[0].datas);
            //     //
            //     this.pages = _pages;
            //     this.initialized = true;
             
            // },
            //
            pageChange(i){
                // Array.from()  
                // 
                var _pages = this.pages;
                var _pdatas = this.pages.pageData;
                var _datas = this.datas;

                // 
                 _pages.pageNum = i;
                 _pages.pageSize = _datas[i-1].datas.length;
                 _pdatas = Array.from(_datas[i-1].datas)
                 //
                 for(var j = 0;j<_datas[i-1].datas.length;j++){
                    _pdatas[j].totalPrice= _pdatas[j].count*_pdatas[j].unitPrice;
                    
                }
                //
                this.pages = _pages;
                this.pages.pageData = _pdatas;
                this.initialized = true;
            },

            
    },
    beforeMount:function (){
       // this.datas 
       //,initialized false mountedmounteddatas  
       //*************************
       
    },
    mounted:function () {  
       if(!this.initialized){
           // 
            this.pageChange(1);
       }
       
        //  this.datas  v-if
        
    },
    beforeUpdate:function(){
        //this.datas  
        //   
        
    },
    updated:function (){
        
    },
}
</script>

<style>

</style>


