<!--
 * @Author: coocase
 * @Date: 2021-05-02 18:35:13
 * @LastEditTime: 2021-06-21 17:59:36
 * @LastEditors: coocase
 * @Description: 
 * @FilePath: \git\dami\src\pages\detail\detailIntro.vue
-->
<template>
    <div class="detail-intro">
        <Row>
            <Col span="18">
                <div class="detail-gallery">
                    <Row>
                        <Col span="8">
                            <img
                                class="item-img"
                                :style="{ 'background-color': datas.imgUrl }"
                            />
                        </Col>
                        <Col span="16">
                            <div class="item-title">{{ datas.itemName }}</div>
                            <div class="item-basicDesc">
                                "{{ datas.basicDescription }}"
                            </div>
                            <div class="item-state">
                                <Steps :current="datas.state-1">
                                    <Step
                                        title=""
                                        
                                    ></Step>
                                    <Step
                                        title="/"
                                        
                                    ></Step>
                                    <Step
                                        title=""
                                        
                                    ></Step>
                                    <Step
                                        title=""
                                        
                                    ></Step>
                                </Steps>
                            </div>
                            <div class="item-adress">{{datas.adress}} XXXX</div>
                            <div class="item-date">
                                 :
                                <!-- v-model -->
                                <RadioGroup
                                    type="button"
                                    v-for="time in datas.startTime"
                                    :key="time.time"
                                    @on-change="radioChange"
                                    class="radio"
                                    v-model="timeSelect"
                                >
                                    <!-- labelidradio -->
                                    <Radio :label="time.id" class="radio-item">{{
                                        time.time
                                    }}</Radio>
                                </RadioGroup>
                            </div>
                            <div class="item-price">
                                 
                                <RadioGroup
                                    type="button"
                                    v-for="price in datas.itemPrice"
                                    :key="price.price"
                                    @on-change="radioChange"
                                    class="radio"
                                    v-model="priceSelect"
                                >
                                    <!-- labelidradio -->
                                    <Radio :label="price.id" class="radio-item">{{
                                        price.price
                                    }} </Radio>
                                </RadioGroup>
                            </div>
                            <Button class="item-buy" type="primary"  @click="getBuyInfo"></Button>
                        </Col>
                    </Row>
                </div>
            </Col>
            <Col span="1"></Col>
            <Col span="5">
                <div class="note">
                    <p class="note-title">
                        <Icon
                            type="ios-checkmark-circle-outline"
                            color="red"
                        />
                    </p>
                    <p class="note-info">
                        
                    </p>
                    <p class="note-title">
                        <Icon
                            type="ios-checkmark-circle-outline"
                            color="red"
                        />
                    </p>
                    <p class="note-info">
                        <br />
                        1. <br />
                        2. 
                        
                    </p>
                    <p class="note-title">
                        <Icon type="ios-checkmark-circle-outline" color="red" />
                        
                    </p>
                    <p class="note-info">
                        
                    </p>
                    <p class="note-title">
                        <Icon
                            type="ios-checkmark-circle-outline"
                            color="red"
                        />
                    </p>
                    <p class="note-info">
                        1
                    </p>
                </div>
            </Col>
        </Row>
    </div>
</template>

<script>
export default {
    name: "detailIntro",
    props: ["datas"],
    data: function () {
        return {
            score: this.datas.avgScore / 2,
            timeSelect: 1,
            priceSelect:1,
            isDisabled:true,
        };
    },
    mounted: function () {
        // console.log(this.datas);
        // console.log("intro");
        this.judgeDisabled()
    },
    methods: {
        radioChange: function (e) {
        },
        judgeDisabled:function () {  
            // console.log(this.datas.state);
            if(this.datas.state==2&&this.datas.state==3){
               return true
            }else{
                return true
            }
           
        },
        getBuyInfo:function () {  
            if(this.GLOBAL.hasLogin){
                this.$emit('getBuyInfo',1)
                
                this.$router.push({ name: 'Pay' })
            }else{
                 this.$Notice.error({
                    title: '',
                    duration:3
                });
            }
            
        }
         
    },
};
</script>

<style scoped>
.item-img {
    height: 360px;
    width: 270px;
    /* background-color: rgb(130, 145, 116); */
}
.item-title {
    font-size: 22px;
    color: #000;
}
.item-basicDesc {
    position: relative;
    font-size: 16px;
    color: #4a4a4a;
    margin: 21px 0px;
    zoom: 1;
}
.item-state {
    margin: 40px 0px;
}
.radio-item{
margin-left: 10px;
}
.item-date,.item-adress,.item-price{
    position: relative;
    font-size: 16px;
    color: #4a4a4a;
    margin-top: 21px;
    zoom: 1;
}

.item-buy {
    margin-top: 40px;
    margin-left: 50px;
    width: 210px;
    height: 40px;
    line-height: 40px;
    /* background-color: #ff3c1b ; */
    color: #fff;
    font-size: 14px;
    font-weight: 400;
    border-radius: 3px;
}

.note-title {
    font-size: 14px;
}
.note-info {
    font-size: 12px;
    color: #999;
    margin-bottom: 10px;
}
</style>


